<template>
    <div id="common-box">
        <div class="check-box">
            <h2 class="check-title">邮箱验证</h2>
            <div class="check-form">
                <!-- 验证信息 -->
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
                    <el-form-item label="您的邮箱号" prop="inputEmail">
                        <div class="mycheck-input">
                            <el-input v-model="ruleForm.inputEmail"></el-input>
                            <el-button size="mini"  @click="sendMail">获取邮箱验证码</el-button>
                        </div>
                    </el-form-item>
                    <el-form-item label="邮箱验证码" prop="inputCode">
                        <el-input v-model="ruleForm.inputCode" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="success" @click="codeMail('ruleForm')">验证邮箱</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            ruleForm:{
                inputEmail:'',
                inputCode:''
            },
            rules:{
                inputCode:[
                    { required:true,message:'请输入邮箱验证码',trigger:'blur'}
                ]
            }
        }
    },
    methods:{
        sendMail(){
            let that=this;
            this.$api.getmailcode(that.ruleForm.inputEmail).then(res=>{
                if(res.err_code === 100){
                    that.$message('邮箱验证码发送成功，请注意查收');
                }
            })
        },
        codeMail(formVal){
            let that=this;
            this.$refs[formVal].validate((valid)=>{
                if(valid){
                this.$api.mailcode(
                    that.ruleForm.inputEmail,
                    that.ruleForm.inputCode).then(res=>{
                        if(res.err_code === 100){
                            that.$message('邮箱认证成功');
                        }
                    })
                }
            })
        },
        getUserInfo(){
            let that=this;
            that.$api.userinfo().then(res=>{
                that.ruleForm.inputEmail=res.data.user.userEmail;
            })
        }
    },
    created(){
        this.getUserInfo();
    }
}
</script>

<style lang="scss" scoped>
   
</style>